}

section {
  width: 100%;
  box-sizing: border-box;
  min-height: 33vh;
}
section .section-wrapper {
  padding: 2px;
  box-sizing: border-box;
  color: #ffff;
  height: 100%;
  width: 100%;
}

.line {
  width: 90%;
  height: 4px;
  background: linear-gradient(to right, #c0392b 13.25%, #fbfcfc 13.25%);
  margin-top: 20vh;
  margin-left: 5%;
}
.line:hover {
  cursor: pointer;
}
.line .year {
  background-color: #fbfcfc;
  border-radius: 50%;
  float: left;
  position: relative;
  left: 5.3%;
  box-sizing: border-box;
  margin-right: calc(2.65% - 12.5px);
  margin-left: calc(2.65% - 12.5px);
  width: 25px;
  height: 25px;
  top: -10px;
}
.line .year::before {
  content: attr(data-timelinelabel);
  position: absolute;
  top: -75px;
  left: -5px;
  width: 150px;
  transform: rotateZ(-45deg);
  display: block;
  font-size: 0.8em;
  font-weight: bold;
}
.line .year::after {
  content: attr(data-mobiletext);
  position: absolute;
  top: 23px;
  left: -15px;
  padding: 10px;
}
.line .quarter {
  background-color: #fbfcfc;
  border-radius: 50%;
  float: left;
  position: relative;
  left: 5.3%;
  box-sizing: border-box;
  margin-right: calc(2.65% - 7.5px);
  margin-left: calc(2.65% - 7.5px);
  width: 15px;
  height: 15px;
  top: -5px;
}
.line .complete {
  background-color: #c0392b;
}
.line .dot.active {
  background-color: #c0392b;
  cursor: pointer;
}

#timeline {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: #1a1531;
  color: #eeff00;
}

.year-header {
  margin-top: auto;
  font-size: 20px;
  text-align: center;
  margin-bottom: 15px;
}

#description {
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 3%;
  min-height: 50vh;
  background-color: #1a1531;
}
#description h3 {
  color: #c0392b;
  font-weight: 600;
}
#description .section-wrapper > div {
  display: none;
}
#description .section-wrapper > div.text-default {
  /* display: block; */
  padding: 2%;
}

@media all and (max-width: 760px) {
  .line .year {
    width: 20px;
    height: 20px;
    margin-right: calc(2.65% - 10px);
    margin-left: calc(2.65% - 10px);
    top: -8px;
  }
  .line .quarter {
    width: 10px;
    height: 10px;
    margin-right: calc(2.65% - 5px);
    margin-left: calc(2.65% - 5px);
    top: -3px;
  }
}
@media all and (min-width: 481px) {
  .quarter.q1 {
    display: none;
  }

  .dot:hover {
    background-color: #c0392b;
    cursor: pointer;
  }
}
@media all and (max-width: 480px) {
  #timeline {
    padding-bottom: 20px;
    padding-top: 0;
  }

  .line {
    height: auto;
    background: transparent;
    margin-top: 20px;
  }
  .line .year,
  .line .quarter {
    border-radius: 0;
    float: none;
    position: static;
    margin-right: 0;
    margin-left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 42px;
    text-align: center;
    transition: background-color 500ms;
  }
  .line .year::after,
  .line .quarter::after {
    content: attr(data-mobiletext);
    display: block;
    padding-top: 12px;
  }
  .line .year.complete,
  .line .quarter.complete {
    background-color: #c0392b;
    color: #fbfcfc;
  }
  .line .year.active,
  .line .year.complete.active,
  .line .quarter.active,
  .line .quarter.complete.active {
    background-color: #c0392b;
    color: #fbfcfc;
  }
  .line .year {
    display: block;
  }
  .line .year::before {
    display: none;
  }
  .line .year::after {
    position: static;
  }
  .line .quarter {
    display: none;
  }
  .line .quarter.y2017 {
    display: block;
  }
}

