* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loader {
  background-color: rgb(208, 191, 255);
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  font-family: "Poppins", sans-serif;
}

.header {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.navbar {
  z-index: 10;
  width: 100%;
  height: 75px;
  position: fixed;
  background-color: #1f2235;
}

.navbar_container {
  display: flex;
  height: 75px;
  align-items: center;
  justify-content: space-between;
}

.navbar_left-name {
  width: 100%;
  font-size: 30px;
  font-weight: 600;
  color: aqua;
  text-decoration: aqua;
  margin-left: 10%;
}

.navbar_right {
  display: flex;
  list-style-type: none;
  margin-right: 5%;
}

.navbar_right li a {
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  font-size: 18px;
  padding: 0 15px;
}

/* Helpers */
.container {
  width: 100%;
  margin: 0;
  padding: 0;
}

/* Grid System */
.row {
  display: flex;
  width: 100%;
  position: relative;
}
.col-6 {
  width: 50%;
  height: 100%;
}
.col-12 {
  width: 100%;
}

/* Header */

.social-icon {
  display: inline-block;
  height: 40px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: 0 10px 10px 0;
  text-align: center;
  line-height: 40px;
  border-radius: 50%;
  background: none;
  color: #132d70;
  transition: all 0.5s ease;
}
.social-icon:hover {
  color: #ff4a57;
  background-color: #ffffff;
  background: none;
}

.header_content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 100vh;
  margin-left: 5vh;
  margin-top: 10%;
  width: 100%;
  color: #000000;
}

.header_ul {
  list-style-type: none;
  display: flex;
  align-self: center;
}

.header_ul li {
  width: 50px;
  height: 50px;
  /* border: 1px solid #ff4a57; */
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
}

.header_ul {
  list-style-type: none;
  display: flex;
  margin-bottom: 20px;
}

.header_section h1 {
  font-size: 57px;
  font-weight: 700;
  margin-bottom: 20px;
}

.header_section p {
  font-size: 26px;
  font-weight: 300;
  line-height: 40px;
  margin-bottom: 50px;
}

.btn {
  outline: none;
  border: none;
  color: #000000;
  padding: 13px 12px;
  text-decoration: none;
}

.btn-outline {
  border: 2px solid #ff4a57;
  border-radius: 50px;
}

.btn-smart {
  background: #ff4a57;
  padding: 13px 25px;
  border-radius: 50px;
}

.play {
  color: #fff;
  font-size: 25px !important;
  padding-top: 10px;
}

.banner_img {
  position: relative;
  padding: 0;
  margin-top: 20vh;
  margin-bottom: 20vh;
  height: 100vh;
  overflow: hidden;
}

/* Services */

.services {
  background: #1f2235;
  padding: 20px;
  color: #fff;
}

.services_container {
  height: 125vh;
  width: 100%;
}

.common {
  width: 50%;
  margin: 20px auto;
  text-align: center;
}

.heading {
  margin-bottom: 20px;
  font-weight: 500;
  color: #ff4a57;
  font-size: 16px;
}
.mainHeader {
  font-size: 32px;
  margin-bottom: 20px;
  margin-top: 10px;
  color: #fff;
}
.mainContent {
  font-size: 16px;
  color: #dfdfdf;
}
.commonBorder {
  background: #ff4a57;
  height: 3px;
  width: 100px;
  margin: 30px auto;
}

.services__box {
  padding: 40px;
  background: #23263a;
  color: #fff;
  margin: 20px;
  transition: all 0.5s linear;
}
.services__box:hover {
  background: #ff4a57;
}
.commonIcons {
  font-size: 40px;
  color: #ff4a57;
  margin-bottom: 15px;
}
.services__box:hover .commonIcons {
  color: #fff;
}
.services__box-header {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 15px;
}
.services__box-p {
  font-size: 14px;
  color: #dfdfdf;
}

/*About*/
.about {
  /* width: 100%; */
  height: 125vh;
  background: #23263a;
  /* padding: 10px; */
  padding-top: 10px;
}
.about__img {
  padding: 30px;
}
.about__img img {
  width: 300px;
  height: 400px;
}
.about__info {
  text-align: center;
  align-items: center;
  color: #fff;
}
.about__info h1 {
  margin-bottom: 30px;
}
.about__info-p1 {
  font-family: "Akaya Kanadaka", cursive;
  font-size: 14px;
  margin-bottom: auto;
}
.about__info-p2 {
  font-family: "Akaya Kanadaka", cursive;
  font-size: 14px;
}

.info__contacts {
  margin-top: 20px;
}
.info__contacts strong {
  color: #ff4a57;
}
.info__contacts p {
  margin-top: 3px;
}
.info__contacts .col-6 {
  margin: 10px 0;
}

.renderCards {
  display: flex;
  height: auto;
  margin-top: auto;
  width: 100vw;
  text-align: center;
  align-items: center;
  align-self: center;
  transform: scale(0.8);
}

@media (max-width: 765px) {
  .container {
    width: 100%;
    padding: 0;
  }
  .col-6 {
    width: 100%;
  }
  .col-3 {
    width: 100%;
  }
  .col-4 {
    width: 20%;
  }
  .navbar__right {
    flex-direction: column;
    width: 100%;
    align-items: center;
    background: #1f2235;
    position: fixed;
    top: 0;
    left: 0;
  }
  .navbar__right li a {
    display: block;
    padding: 20px 0;
    font-size: 14px;
  }
  .toggle {
    display: flex;
  }
  .banner__img img {
    display: none;
  }
  .header__section h1 {
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  .header__section p {
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;
    margin-bottom: 50px;
  }
  .common {
    width: 100%;
  }
  .h-650 {
    height: auto;
  }
}
